import { AuthService } from 'src/app/services/auth.service';
import { OrderService } from 'src/app/services/order.service';
import { Component, OnInit } from '@angular/core';
import { Order } from 'src/app/models/order';

@Component({
  selector: 'app-supervisor-orders-filter',
  templateUrl: './supervisor-orders-filter.component.html',
  styleUrls: ['./supervisor-orders-filter.component.scss'],
})
export class SupervisorOrdersFilterComponent implements OnInit {
  constructor(
    private orderService: OrderService,
    private authService: AuthService
  ) {}

  orders: Order[];
  ngOnInit(): void {}

  onDateChange(val) {
    let sub = this.orderService.getOrdersByDate(val).subscribe((a) => {
      this.orders = a;
      sub.unsubscribe();
    });
  }
}
